.App {
  text-align: center;
  margin: 0 200px 75px 200px;
}

/* .gallery-container {
  margin: 0 150px;
} */

h1 {
  font-size: xxx-large;
}

hr {
  margin-top: 25px;
  margin-bottom: 25px;
}

html {
  scroll-behavior: smooth;
}

.navbar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -15px;
}

.navbar a {
  margin: 0 25px; /* Adjust spacing between items */
  text-decoration: none;
  color: #333;
}

.navbar span {
  color: #333;
}




.pricing-container {
  text-align: left;
  display: flex; /* Use flexbox to align items */
}
.price {
  width: 50%; /* Each box takes half of the container's width */
 
  display: flex; /* Use flexbox for box content alignment */
  justify-content: center; /* Center content horizontally */
  align-items: center; /* Center content vertically */
  flex-direction: column;
}



.left {
  width: calc(50% - 1px); /* Subtract 1px to account for the dividing line */
  box-sizing: border-box;
  position: relative; /* Required for positioning the pseudo-element */
}
.left::after {
  content: '';
  position: absolute;
  top: 0;
  right: -1px; /* Position the dividing line */
  height: 100%;
  width: 2px; /* Width of the dividing line */
  background-color: #555554; /* Color of the dividing line */
}


@media only screen and (max-width: 1200px) {
  .App {
    margin: 0 100px 50px 100px;
  }
}

@media only screen and (max-width: 1200px) {
  .App {
    margin: 0 50px 50px 50px;
  }
}

@media only screen and (max-width: 850px) {
  .App {
    margin: 0 15px 50px 15px;
  }
  .price {
    text-align: left;
    margin-left: auto;
    margin-right: auto;
    width: 100%;
  }
  .pricing-container {
    flex-direction: column;
    text-align: center;
    justify-content: center;
  }
  .left::after {
    display: none;
  }
}